import { FunctionComponent, useMemo, type CSSProperties } from "react";
import { Checkbox, FormControlLabel } from "@mui/material";
import styles from "./BookingOptionsCard.module.css";

type BookingOptionsCardType = {
  stopOptionsText?: string;

  /** Style props */
  propWidth?: CSSProperties["width"];
  propWidth1?: CSSProperties["width"];
  propWidth2?: CSSProperties["width"];
  propAlignItems?: CSSProperties["alignItems"];
};

const BookingOptionsCard: FunctionComponent<BookingOptionsCardType> = ({
  stopOptionsText,
  propWidth,
  propWidth1,
  propWidth2,
  propAlignItems,
}) => {
  const stopsStyle: CSSProperties = useMemo(() => {
    return {
      width: propWidth,
    };
  }, [propWidth]);

  const resultsStyle: CSSProperties = useMemo(() => {
    return {
      width: propWidth1,
    };
  }, [propWidth1]);

  const frameStyle: CSSProperties = useMemo(() => {
    return {
      width: propWidth2,
      alignItems: propAlignItems,
    };
  }, [propWidth2, propAlignItems]);

  return (
    <div className={styles.stops} style={stopsStyle}>
      <b className={styles.results} style={resultsStyle}>
        {stopOptionsText}
      </b>
      <div className={styles.frame} style={frameStyle}>
        <FormControlLabel
          label="1 Stop"
          labelPlacement="end"
          control={<Checkbox size="medium" />}
        />
        <FormControlLabel
          label="2 Stops"
          labelPlacement="end"
          control={<Checkbox size="medium" />}
        />
      </div>
    </div>
  );
};

export default BookingOptionsCard;
