import { FunctionComponent, useMemo, type CSSProperties } from "react";
import styles from "./BookingCardContainer.module.css";

type BookingCardContainerType = {
  flightIconUrl?: string;
  airlineLogoUrl?: string;
  price?: string;
  airportCode?: string;
  locationName?: string;
  flightCode?: string;
  cityName?: string;
  seatType?: string;
  passengerCount?: string;
  bookingPlatformName?: string;
  timeAgoText?: string;

  /** Style props */
  propTextAlign?: CSSProperties["textAlign"];
};

const BookingCardContainer: FunctionComponent<BookingCardContainerType> = ({
  flightIconUrl,
  airlineLogoUrl,
  price,
  airportCode,
  locationName,
  flightCode,
  cityName,
  seatType,
  passengerCount,
  bookingPlatformName,
  timeAgoText,
  propTextAlign,
}) => {
  const arrNameStyle: CSSProperties = useMemo(() => {
    return {
      textAlign: propTextAlign,
    };
  }, [propTextAlign]);

  return (
    <div className={styles.bookingCard}>
      <div className={styles.airlinePrice}>
        <div className={styles.airline}>
          <img className={styles.airlinelogoIcon} alt="" src={flightIconUrl} />
          <div className={styles.singaporeAirlines}>{airlineLogoUrl}</div>
        </div>
        <div className={styles.price}>{price}</div>
      </div>
      <div className={styles.locations}>
        <div className={styles.departure}>
          <div className={styles.depcode}>{airportCode}</div>
          <div className={styles.depname}>{locationName}</div>
        </div>
        <img className={styles.flightIcon} alt="" src="/flight-icon.svg" />
        <div className={styles.arrival}>
          <div className={styles.depcode}>{flightCode}</div>
          <div className={styles.arrname} style={arrNameStyle}>
            {cityName}
          </div>
        </div>
      </div>
      <div className={styles.details}>
        <div className={styles.class}>
          <img className={styles.classIcon} alt="" src="/class-icon.svg" />
          <div className={styles.singaporeAirlines}>{seatType}</div>
        </div>
        <div className={styles.class}>
          <img className={styles.classIcon} alt="" src="/calender.svg" />
          <div className={styles.singaporeAirlines}>{passengerCount}</div>
        </div>
      </div>
      <div className={styles.provider}>
        <div className={styles.singaporeAirlines}>
          <span>{`Booked on `}</span>
          <span className={styles.expedia}>{bookingPlatformName}</span>
        </div>
        <div className={styles.timeago}>{timeAgoText}</div>
      </div>
    </div>
  );
};

export default BookingCardContainer;
